.tab-label{
  display:flex;
  justify-content:space-between;
  padding:1em;
  background:#2c3e50;
  font-weight:bold;
  cursor:pointer
}

.tab-label:hover{
  background:#1a252f
}

.tab-label::after{
  content:"\276F";
  width:1em;
  height:1em;
  text-align:center;
  transition:all .35s
}

.tab-content{
  max-height:0;
  padding:0 1em;
  color:#2c3e50;
  background:white;
  transition:all .35s
}

.tab-close{
  display:flex;
  justify-content:flex-end;
  padding:1em;
  font-size:0.75em;
  background:#2c3e50;
  cursor:pointer
}

.tab-close:hover{
  background:#1a252f
}

input:checked+.tab-label{
  background:#1a252f
}

input:checked+.tab-label::after{
  transform:rotate(90deg)
}

input:checked ~ .tab-content{
  max-height:100vh;
  padding:1em
}

